html {
  font-family: "Inter", sans-serif;
  line-height: 1.42;
  font-size: 16px;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  height: 100%;
  color: #383838;
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 16px;
  font-size: 1rem;
  line-height: 1.42;
  background: #FFF;
}

input::-ms-clear {
  display: none;
}

/* Для Safari */
input[type=search]::-webkit-search-cancel-button {
  -webkit-appearance: none;
}

p {
  margin: 0px 0px 24px;
}
@media (max-width: 599px) {
  p {
    margin-bottom: 12px;
  }
}

ul, ol {
  padding: 0px;
  margin: 0px;
  list-style: none;
}

li {
  list-style: none;
}

a {
  color: #383838;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6, .h20 {
  font-family: "Inter";
  font-weight: 700;
  margin: 0px 0px 28px;
  text-transform: none;
  line-height: 1.2;
}

h1, .h1 {
  font-size: 48px;
  font-size: 3rem;
  margin-top: 0;
  line-height: 1.15;
}
@media (max-width: 960px) {
  h1, .h1 {
    font-size: 42px;
    font-size: 2.625rem;
  }
}
@media (max-width: 599px) {
  h1, .h1 {
    font-size: 32px;
    font-size: 2rem;
  }
}

h2, .h2, .ui-h2 {
  font-size: 32px;
  font-size: 2rem;
  line-height: 1.2;
}

h3, .h3, .ui-h3 {
  font-size: 28px;
  font-size: 1.75rem;
  line-height: 1.2;
}

h4, .h4, .ui-h4 {
  font-size: 24px;
  font-size: 1.5rem;
}
@media (max-width: 599px) {
  h4, .h4, .ui-h4 {
    font-size: 22px;
    font-size: 1.375rem;
  }
}

h5, .h5 {
  font-size: 20px;
  font-size: 1.25rem;
}

h6, .h6, .ui-h5 {
  font-size: 18px;
  font-size: 1.125rem;
  text-transform: none;
  font-weight: 700;
}
@media (max-width: 599px) {
  h6, .h6, .ui-h5 {
    font-size: 16px;
    font-size: 1rem;
  }
}

.ui-font-midi {
  font-size: 16px;
  font-size: 1rem;
}

.ui-font-mini {
  font-size: 14px;
  font-size: 0.875rem;
}

.h20 {
  font-size: 20px;
  font-size: 1.25rem;
}

.ui-strong,
.ui-bold {
  font-weight: 700;
}

.ui-demi {
  font-weight: 600;
}

a {
  text-decoration: none;
  outline: none;
  transition: color 0.3s, border 0.3s;
}

b, strong {
  font-weight: 700;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper a img {
  border: none;
  display: block;
  line-height: 0;
  font-size: 0;
}

/*---- ICONS ---- */
::selection {
  background: #70B12F;
  color: #FFF;
}

::-moz-selection {
  background: #F4FAFE;
  color: #FFF;
}

@media (min-width: 992px) {
  ::-webkit-scrollbar {
    width: 8px;
    height: 8px;
  }

  ::-webkit-scrollbar-thumb {
    width: 8px;
    height: 8px;
    background-color: #70B12F;
  }

  ::-webkit-scrollbar-thumb:hover {
    background: #84cc3d;
  }
}
.page-title {
  font-size: 32px;
  font-size: 2rem;
  color: #484949;
  font-weight: 600;
  margin-bottom: 20px;
}

.block-title {
  margin-bottom: 6px;
  color: #484949;
}
.block-title__center {
  text-align: center;
}

.block-subtitle {
  margin-bottom: 24px;
  color: rgba(56, 56, 56, 0.5);
}
@media (max-width: 599px) {
  .block-subtitle {
    margin-bottom: 14px;
  }
}
.block-subtitle__center {
  text-align: center;
}

.ui-search-group {
  background-color: #FFF;
  border-radius: 8px;
  height: 52px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.ui-search-group .ui-select {
  transition: background-color 0.3s, height 0.3s;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
}
.ui-search-group .ui-select::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 1px;
  height: 80%;
  background-color: #e1e3e5;
  transform: translateY(-50%);
}
.ui-search-group .ui-select .jq-selectbox__dropdown {
  margin-top: 20px;
  padding-top: 14px;
  padding-bottom: 14px;
  padding-left: 0;
  padding-right: 0;
  min-width: 200px;
}
.ui-search-group .ui-select .jq-selectbox__dropdown li {
  padding: 8px 24px;
  display: flex;
  justify-content: space-between;
}
.ui-search-group .ui-select .jq-selectbox__dropdown li.sel {
  color: #70B12F;
}
.ui-search-group .ui-select .jq-selectbox__dropdown li.sel::after {
  content: "";
  background-image: url(../images/svg/check_green.svg);
  background-repeat: no-repeat;
  background-position: center right;
  background-size: 24px 24px;
  width: 24px;
  height: 24px;
}
.ui-search-group .ui-select .jq-selectbox__dropdown li:hover {
  background-color: #F0F2F3;
}
.ui-search-group .ui-select.opened {
  background-color: #E1E3E5;
}
.ui-search-group .ui-select.opened::after {
  height: 100%;
  background-color: rgba(56, 56, 56, 0.1);
}
.ui-search-group-input {
  border: none;
  outline: none;
  flex-grow: 1;
  width: 50px;
  padding-left: 20px;
  padding-right: 20px;
  transition: background-color 0.3s;
}
.ui-search-group-input:hover, .ui-search-group-input:focus {
  background-color: #f5f6f7;
  box-shadow: 0px 10px 25px rgba(22, 25, 50, 0.07);
}
.ui-search-group .ui-btn {
  height: 52px;
}

.ui-btn {
  color: #fff;
  border: none;
  outline: none;
  border-radius: 8px;
  font-weight: 600;
  padding-left: 24px;
  padding-right: 24px;
  cursor: pointer;
  transition: background-color 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  height: 48px;
  white-space: nowrap;
}
@media (max-width: 599px) {
  .ui-btn {
    padding-left: 15px;
    padding-right: 15px;
  }
}
.ui-btn .icon {
  width: 24px;
  height: 24px;
  fill: none;
  stroke-width: 1.5px;
}
.ui-btn__sm {
  font-size: 14px;
  font-size: 0.875rem;
  border-radius: 5px;
  padding: 6px 12px;
  height: 36px;
}
.ui-btn__sm .icon {
  width: 16px;
  height: 16px;
}
.ui-btn__green {
  background-color: #70B12F;
}
.ui-btn__green:hover {
  background-color: #619829;
}
.ui-btn__orange {
  background-color: #F79E1B;
}
.ui-btn__orange:hover {
  background-color: #E68900;
}
.ui-btn__translucent {
  background-color: rgba(255, 255, 255, 0.2);
}
.ui-btn__translucent:hover {
  background-color: rgba(255, 255, 255, 0.4);
}

.ui-input {
  border: 1px solid #e1e3e5;
  outline: none;
  border-radius: 8px;
  padding-left: 20px;
  padding-right: 20px;
  height: 48px;
  transition: background-color 0.3s;
}
.ui-input:hover, .ui-input:focus {
  background-color: #f5f6f7;
  box-shadow: 0px 10px 25px rgba(22, 25, 50, 0.07);
}

.ui-select {
  position: relative;
  color: #383838;
  height: 100%;
  cursor: pointer;
}
.ui-select__filter {
  border: 1px solid #e1e3e5;
  border-radius: 6px;
  height: 44px;
  color: rgba(56, 56, 56, 0.5);
}
.ui-select__filter .jq-selectbox__trigger-arrow {
  opacity: 0.5;
}
.ui-select__filter .jq-selectbox__dropdown li.sel {
  color: #70B12F;
}
.ui-select__filter .jq-selectbox__dropdown li.sel::after {
  content: "";
  background-image: url(../images/svg/check_green.svg);
  background-repeat: no-repeat;
  background-position: center right;
  background-size: 24px 24px;
  width: 24px;
  height: 24px;
}
.ui-select__pagination {
  border: 1px solid #e1e3e5;
  border-radius: 8px;
  height: 42px;
  min-width: 70px;
}
.ui-select__pagination .jq-selectbox__select {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 12px;
  padding-left: 13px;
}
.ui-select__pagination .jq-selectbox__select-text {
  padding-right: 8px;
}
.ui-select__pagination .jq-selectbox__dropdown {
  margin-top: 2px;
  margin-bottom: 2px;
  padding-top: 2px;
  padding-bottom: 2px;
}
.ui-select__pagination .jq-selectbox__dropdown ul {
  border-radius: 8px;
}
.ui-select__pagination .jq-selectbox__dropdown li.sel::after {
  display: none;
}

.jq-selectbox.opened .jq-selectbox__trigger-arrow {
  transform: rotate(180deg);
}
.jq-selectbox__dropdown {
  background-color: #FFF;
  border-radius: 8px;
  width: 100%;
  margin-top: 12px;
  padding-top: 14px;
  padding-bottom: 14px;
  padding-left: 0;
  padding-right: 0;
  box-shadow: 0px 20px 50px rgba(22, 25, 50, 0.07);
}
.jq-selectbox__dropdown li {
  padding: 8px 24px;
  display: flex;
  justify-content: space-between;
}
.jq-selectbox__dropdown li.sel {
  color: #70B12F;
}
.jq-selectbox__dropdown li.sel::after {
  content: "";
  background-image: url(../images/svg/check_green.svg);
  background-repeat: no-repeat;
  background-position: center right;
  background-size: 24px 24px;
  width: 24px;
  height: 24px;
}
.jq-selectbox__dropdown li:hover {
  background-color: #F0F2F3;
}
.jq-selectbox__trigger-arrow {
  background-image: url(../images/svg/arrow-down.svg);
  background-repeat: no-repeat;
  background-position: center right;
  width: 16px;
  height: 16px;
  transition: transform 0.3s;
}
.jq-selectbox__select {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 20px;
  padding-left: 20px;
}
.jq-selectbox__select-text {
  padding-right: 8px;
}

.ui-centered {
  text-align: center;
}

.pagination {
  font-weight: 600;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  flex-wrap: wrap;
  row-gap: 8px;
}
.pagination-list {
  display: flex;
  gap: 10px;
  transform: translateX(-50%);
  margin-left: 50%;
}
.pagination-item-link {
  color: #383838;
  display: block;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #F0F2F3;
  transition: background-color 0.3s;
}
.pagination-item-link.active {
  background-color: #70B12F;
  color: #fff;
}
.pagination-item-link:not(.active):hover {
  background-color: #e1e3e5;
}
.pagination-item-nav {
  width: 44px;
  height: 44px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #F0F2F3;
  transition: background-color 0.3s;
}
.pagination-item-nav:not(.disabled):hover {
  background-color: #e1e3e5;
}
.pagination-item-nav .icon {
  width: 24px;
  height: 24px;
  stroke: #383838;
}
.pagination-item-nav__prev {
  transform: rotate(-180deg);
}
.pagination-item-nav.disabled {
  cursor: not-allowed;
}
.pagination-item-nav.disabled .icon {
  stroke: rgba(56, 56, 56, 0.5);
}
.pagination-item-dots {
  height: 44px;
  line-height: 34px;
}
.pagination-count {
  display: flex;
  align-items: center;
  margin-left: auto;
  gap: 14px;
}

.breadcrumbs {
  margin-bottom: 18px;
}
.breadcrumbs li {
  display: inline-block;
  margin-right: 24px;
  position: relative;
  font-size: 16px;
  font-size: 1rem;
}
.breadcrumbs li a {
  transition: color 0.3s;
  color: rgba(56, 56, 56, 0.5);
}
.breadcrumbs li a:hover {
  color: #383838;
}
.breadcrumbs li:not(:last-child)::after {
  position: absolute;
  top: 50%;
  right: -14px;
  transform: translate(50%, -50%);
  content: "";
  background-image: url(../images/svg/icon-chevron-right_grey.svg);
  width: 18px;
  height: 18px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 18px 18px;
}
.breadcrumbs li:last-child {
  color: #484949;
  font-weight: 600;
}

